import {
  Component,
  OnInit,
  style,
  state,
  animate,
  transition,
  trigger
}

from '@angular/core';
import {
  Portfolio
}

from "../assets/model/portfolio.interface";
@Component({
selector: 'app-root',
templateUrl: './app.component.html',
styleUrls: ['./app.component.css'],
animations: [trigger('fadeInOut', [transition('void => *', [style({
      opacity: 0
    }),
    animate(500, style({
      opacity: 1
    }))
  ]),
  transition('* => void', [animate(500, style({
    opacity: 0
  }))])
])]
}

) export class AppComponent implements OnInit {
  public portfolio: Portfolio;
  public logOpen: boolean = true;
  public updateOpen: boolean = true;
  public bioOpen: boolean = true; //title = 'app';
  public ngOnInit(): void {
    //target portfolio item
    this.portfolio = {
      firstName: "James",
      middleName: "Derric",
      lastName: "Chhun",
      subHeading: "Web Developer | Student",
      socialMedia: [{
        icon: "fa fa-linkedin fa-2x",
        link: "www.facebook.com",
        color: "#0077B5"
      }, {
        icon: "fa fa-github fa-2x",
        link: "www.facebook.com",
        color: "#6e5494"
      }],
      introduction: "Hello, I'm James Chhun and I am a Web Developer and Student based in San Diego.",
      changeLog: [{
        color: "#6e5494",
        month: "11",
        year: 2017,
        highlight: "Turned 21",
        details: "Legit old now"
      }, {
        color: "#6e5494",
        month: "11",
        year: 2017,
        highlight: "Turned 21",
        details: "Legit old now"
      }, {
        color: "#6e5494",
        month: "11",
        year: 2017,
        highlight: "Turned 21",
        details: "Legit old now"
      }],
      updates: [{
        description: "daffasfsa",
        link: "facebook.com"
      }, {
        description: "daffasfsa",
        link: "facebook.com"
      }, {
        description: "daffasfsa",
        link: "facebook.com"
      }],
      speakerBio: "James is a developer"
    };
  }
  public toggleOpen(valueToToggle: string): void {
    switch (valueToToggle) {
      case 'logOpen':
        this.logOpen = !this.logOpen;
        break;
      case 'updateOpen':
        this.updateOpen = !this.updateOpen;
        break;
      case 'bioOpen':
        this.bioOpen = !this.bioOpen;
        break;
    }
  }
}



